import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { FormControl } from '@angular/forms';
import { CdkDragDrop } from '@angular/cdk/drag-drop/drag-events';

import { IDesignerPaletteTree } from './designer-palette.interfaces';

@Component({
  selector: 'rx-designer-palette',
  templateUrl: './designer-palette.component.html',
  styleUrls: ['./designer-palette.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RxDesignerPaletteComponent {
  @Input()
  tree: IDesignerPaletteTree[];

  // TODO-VS: update types
  @Output()
  elementDropped = new EventEmitter<CdkDragDrop<any>>();

  searchField = new FormControl();

  // TODO-VS: update types
  onDropListDropped(dropData: CdkDragDrop<any>): void {
    this.elementDropped.emit(dropData);
  }

  trackByLabelFn<T extends { label: string }>(index, item: T): string {
    return item.label;
  }
}
